import React, { useEffect, useRef, useState, useMemo } from "react";
import { getUserProfile, updateUser } from "../../store/actions/user.action.js";
import { listCities } from "../../store/actions/city.action.js";
import { useNavigate, useLocation } from "react-router-dom";
import SimpleReactValidator from "simple-react-validator";
import { toast } from "react-toastify";

import { useTitle } from '../../helpers/custom-hooks.js';
import { useUserProfile } from '../../context/UserContextProvider.js';
import { CONSTANTS } from '../../helpers/constant.js';

const User = () => {
	const simpleValidator = useRef(new SimpleReactValidator());
	const navigate = useNavigate();
	const { state } = useLocation();
	const { getUserProfile_c } = useUserProfile();
	const USER = useState(getUserProfile_c());
	const updateUserID = useRef(null);

	const [cities, setCities] = useState([]);
	const [passwordType, setPasswordType] = useState("password"); // [password, text]
	const [userForm, setUserForm] = useState({
		name: "",
		cnic: "",
		dob: "",
		email: "",
		phone: "",
		gender: "",
		role: "",
		password: "",
	});


	useEffect(() => {
		// state?.id ? state.id : navigate("/");
		if (!state?.id || !Object.keys(USER).length || USER.role?.toLowerCase().trim() === CONSTANTS.USER) {
			navigate("/");
		} else {
			updateUserID.current = state.id;
			_getUserProfile();
			_getAllCities();
		}
	}, []);
	useTitle("Update User");



	function handleFormChange(event) {
		const { name, value } = event.target;
		simpleValidator.current.showMessageFor(name);
		setUserForm((prevValue) => {
			return {
				...prevValue,
				[name]: value,
			};
		});
	}
	async function _getUserProfile() {
		const response = await getUserProfile(Number(updateUserID.current));
		if (response.status === 200) {
			setUserForm({ ...response.data, password: "" });
		} else {
			toast.error("Unable to fetch User Data");
		}
	}
	async function _getAllCities() {
		let response = await listCities({ _pageNo: 1, _count: 1000 });
		if (response.status === 200) {
			setCities(response.data.cities);
		} else if (response.status >= 400) {
			toast.warn("failed to get cities");
		}
	}

	async function update() {
		let response = await updateUser(userForm, parseInt(userForm.id));
		if (response.status === 200) {
			toast.success("User updated");
			navigate("/list-users");
		} else if (response.status >= 400) {
			toast.warn(response.message.message);
		}
	}

	function handleFormSubmit(e) {
		e.preventDefault();
		if (simpleValidator.current.allValid()) {
			update();
		} else {
			simpleValidator.current.showMessages();
		}
	}

	return (
		<div className="my-8 card">
			<div
				className="card-header border-0 cursor-pointer"
				role="button"
				data-bs-toggle="collapse"
				data-bs-target="#kt_account_profile_details"
				aria-expanded="true"
				aria-controls="kt_account_profile_details"
			>
				<div className="card-title m-0">
					<h3 className="fw-bolder m-0">Update User</h3>
				</div>
			</div>
			<form
				id="kt_account_profile_details"
				className="collapse show"
				onSubmit={(e) => handleFormSubmit(e)}
			>
				<div
					id="kt_account_profile_details_form"
					className="form fv-plugins-bootstrap5 fv-plugins-framework"
					noValidate="novalidate"
				/>
				<div className="card-body border-top p-9">
					<div className="row mb-6"></div>
					<div className="row mb-6">
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Name
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<input
									disabled
									value={userForm.name}
									type="text"
									className="form-control form-control-lg form-control-solid"
									placeholder="Name"
								/>
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								CNIC
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<input
									disabled
									value={userForm.cnic}
									type="text"
									className="form-control form-control-lg form-control-solid"
									placeholder="CNIC"
								/>
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Date of Birth
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<input
									disabled
									value={userForm.dob.split("T")[0]}
									type="text"
									className="form-control form-control-lg form-control-solid"
									placeholder="Date of birth"
								/>
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Gender
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<select
									disabled
									id="gender"
									name="gender"
									value={userForm.gender}
									className="form-control form-control-lg form-control-solid"
								>
									<option value="">Select</option>
									<option value="male">male</option>
									<option value="female">female</option>
								</select>
							</div>
						</div>

						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								City
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<select
									id="city"
									name="city_id"
									value={userForm.city_id}
									className="form-control form-control-lg form-control-solid"
									onChange={handleFormChange}
								>
									<option value="">Select</option>
									{cities.map((city, key) => (
										<option key={key} value={city.id}>{city.name}</option>
									))}
								</select>
								{simpleValidator.current.message(
									"city",
									userForm.city_id,
									"required",
									{ messgae: "City is required", className: "text-danger" }
								)}
							</div>
						</div>

						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Phone
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<input
									onChange={handleFormChange}
									value={userForm.phone}
									type="text"
									name="phone"
									className="form-control form-control-lg form-control-solid"
									placeholder="Contact Number"
								/>
								{simpleValidator.current.message(
									"phone",
									userForm.phone,
									"required|numeric|min:11",
									{ messgae: "Phone is required", className: "text-danger" }
								)}
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Email
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<input
									disabled
									value={userForm.email}
									type="text"
									name="email"
									className="form-control form-control-lg form-control-solid"
									placeholder="Email"
								/>
								{simpleValidator.current.message(
									"email",
									userForm.email,
									"required|email",
									{ messgae: "email is required", className: "text-danger" }
								)}
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Password
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<div className="input-group">
									<input
										id="password"
										className="form-control form-control-lg form-control-solid"
										type={passwordType}
										name="password"
										onChange={handleFormChange}
										value={userForm.password}
										placeholder="password"
									/>
									<span
										className="input-group-text border-0"
										onClick={() =>
											setPasswordType(passwordType === "password" ? "text" : "password")
										}
										style={{ cursor: "pointer" }}
									>
										{passwordType === "password" ? (
											<i className="far fa-eye-slash"></i>
										) : (
											<i className="far fa-eye"></i>
										)}
									</span>
								</div>
								{simpleValidator.current.message(
									"password",
									userForm.password,
									"min:8",
									{ messgae: "password is required", className: "text-danger" }
								)}
								<div className="fv-plugins-message-container invalid-feedback"></div>
							</div>
						</div>
						<div className="row mb-6">
							<div className="col-1"></div>
							<label className="col-lg-2 col-md-2 col-sm-4 col-form-label required fw-bold fs-6">
								Role
							</label>
							<div className="col-lg-9 col-md-9 col-sm-8 fv-row fv-plugins-icon-container">
								<select
									id="role"
									name="role"
									value={userForm.role}
									onChange={handleFormChange}
									className="form-control form-control-lg form-control-solid"
								>
									<option value="">Select</option>
									<option key="1" value="admin">
										{CONSTANTS.RESEARCH_ASSISTANT}
									</option>
									<option key="2" value="user">
										{CONSTANTS.DATA_COLLECTOR}
									</option>
								</select>
								{simpleValidator.current.message(
									"role",
									userForm.role,
									"required",
									{ messgae: "Role is required", className: "text-danger" }
								)}
							</div>
						</div>
					</div>
					<div className="card-footer d-flex justify-content-end py-6 px-9">
						<button
							type="button"
							onClick={(e) => navigate("/list-users")}
							className="btn btn-light btn-active-light-primary me-2"
						>
							Discard
						</button>
						<button
							type="submit"
							className="btn btn-primary"
							id="kt_account_profile_details_submit"
						>
							Submit
						</button>
					</div>
				</div>
			</form>
		</div>
	);
};

export default User;
